import { ITodo } from './Interface';


export const TodoDefault:ITodo = {
    id:NaN,
    title:'',
    content:'',
    time:'',
    completed:false
}

export const GetTimeNow = () => {
    return (new Date()).toJSON().replace(/:([\d]+.[\d]+Z)$/,"")
}


