import { ITodo, ITodoAction } from './../constants/Interface';
import { GETTODOS, GETTODO, EDITETODO, CREATETODO, DELETETODO } from '../constants/ActionType';

export const GetTodos:()=> ITodoAction = ()=>{
    return {
        type: GETTODOS,
        todo: {
            id: 6,
            time: "2018-09-12",
            title: "A04",
            content: "gggggggg",
            completed: false
        }
    }
}

export const GetTodo:(todo: ITodo)=> ITodoAction = (todo: ITodo) => {
    return {
        type: GETTODO,
        todo
    }
}

export const EditeTodo:(todo: ITodo)=> ITodoAction = (todo: ITodo) => {
    return {
        type:EDITETODO,
        todo
    }
}

export const CreateTodo:(todo: ITodo)=> ITodoAction = (todo: ITodo) => {
    return {
        type: CREATETODO,
        todo
    }
}

export const DeleteTodo:(todo: ITodo)=> ITodoAction = (todo: ITodo) => {
    return {
        type: DELETETODO,
        todo
    }
}



