import * as React from 'react';
import { connect } from 'react-redux';
import { ITodo, ITodoAction } from '../../constants/Interface';
import TodoCard from '../smarts/Todocard';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Typography from '@material-ui/core/Typography';
import action from '../../actions';
import { bindActionCreators, Dispatch } from 'redux';
import { TodoDefault, GetTimeNow } from '../../constants/DefaultValue';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


interface IProps {
    Todos: ITodo[]
    edit:(todo:ITodo)=>ITodoAction;
}

class Todos extends React.Component<IProps, React.ComponentState> {
    public state ={
        isShow:false,
        ...TodoDefault
    }
    
    public toggleShow = () => {
        this.setState({isShow:!this.state.isShow})
    }

    public reSetState = () => {
        this.setState(TodoDefault)
    }

    public handleChange = (name:string) => (event:React.ChangeEvent<HTMLTextAreaElement>) => {
        this.setState({
          [name]:event.currentTarget.value,
        })
    }

    public onOpen = () => {
        this.setState({time:GetTimeNow()})
        this.toggleShow()
    }

    public onCreate = () => {
        this.props.edit({
            ...Object.keys(TodoDefault)
            .reduce((obj, key) => {
                obj[key] = this.state[key];
                return obj;
            }, {...TodoDefault})
        })
        this.toggleShow()
    }

    public onCancel = () => {
        this.reSetState()
        this.toggleShow()
    }

    public render() {
        return (
            <div>
                <Grid container={true} direction='column'>
                    <Grid item={true} style={{margin:'auto'}}>
                        <Typography variant='display1'>
                            ToDoList
                        </Typography>
                    </Grid>
                    <Grid item={true} >
                        <Grid container={true} spacing={16} wrap='wrap' style={{margin:'auto'}} >
                        {
                            this.props.Todos.map((x: ITodo, i: number) => {
                                return <TodoCard key={i} {...x} />
                            })
                        }
                        </Grid>
                    </Grid>         
                    <Grid item={true} style={{margin:'auto'}} >
                        <Button variant="fab" onClick={this.onOpen}>
                            <AddIcon />
                        </Button>
                    </Grid>
                    <Dialog
                        open={this.state.isShow}
                        onClose={this.toggleShow}
                        aria-labelledby="form-dialog-title"
                        >
                        <DialogTitle id="form-dialog-title">Create New Todo</DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                To add Todo, please enter your job here.
                            </DialogContentText>
                            <TextField
                                id="datetime-local"
                                label="New appointment"
                                type="datetime-local"
                                defaultValue={this.state.time}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={ this.handleChange('time') }
                                margin="normal"
                            />
                            <TextField
                                label="New Title"
                                defaultValue={this.state.title}
                                InputLabelProps={{
                                shrink: true,
                                }}
                                onChange={ this.handleChange('title') }
                                margin="normal"
                                fullWidth={true}
                            />
                            <TextField
                                label="New Content"
                                defaultValue={this.state.content}
                                InputLabelProps={{
                                shrink: true,
                                }}
                                onChange={ this.handleChange('content') }
                                margin="normal"
                                multiline={true}
                                fullWidth={true}
                            />
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={this.onCreate} color="primary">
                                CREATE
                            </Button>
                            <Button onClick={this.onCancel} color="primary">
                                CANCEL
                            </Button>
                        </DialogActions>
                    </Dialog>
                </Grid>
            </div>
        );
    }
}


function mapStateToProps(state: any) {
    return {
        Todos: state.Todos
    }
}

function mapDispatchToProps(dispatch: Dispatch) {
    return bindActionCreators({
        edit: action.todos.CreateTodo
    }, dispatch)
  }

export default connect(mapStateToProps, mapDispatchToProps)(Todos);



